import React, { useState } from "react";
import { Link } from "react-router-dom";
const Navbar = ({ setActiveTab, activeTab }) => {
    const [isClicked, setIsClicked] = useState("home");
    const handleClick = (val) => {
        setIsClicked(val);
    };
    const handleExportData = () => {
        chrome.storage.local.get("myData", (result) => {
            const data = result.myData;
            if (data) {
                const dataStr = JSON.stringify(data);
                const blob = new Blob([dataStr], { type: "application/json" });
                const url = URL.createObjectURL(blob);
                const a = document.createElement("a");
                a.href = url;
                a.download = "myData.json";
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
                URL.revokeObjectURL(url);
            }
        });
    };
    const handleImportData = (e) => {
        const file = e.target.files[0];
        if (!file)
            return;
        const reader = new FileReader();
        reader.onload = (event) => {
            const importedData = JSON.parse(event.target.result);
            chrome.storage.local.set({ myData: importedData });
        };
        reader.readAsText(file);
    };
    return (React.createElement("div", { className: "flex justify-between headerrow text-2lg" },
        React.createElement("div", { className: "flex items-center" },
            React.createElement("img", { src: "images/display_icon.png", alt: "logo", className: "logo" })),
        React.createElement("div", { className: "w-full" },
            React.createElement("ul", { className: "flex justify-center gap-6" },
                React.createElement("li", null,
                    React.createElement(Link, { to: "/" },
                        React.createElement("p", { className: `py-2 px-3 rounded cursor-pointer ${isClicked == "home" ? "bg-blue-700" : ""}`, onClick: () => handleClick("home") }, "Home"))),
                React.createElement("li", null,
                    React.createElement(Link, { to: "/support" },
                        React.createElement("p", { className: `py-2 px-3 rounded cursor-pointer ${isClicked == "support" ? "bg-blue-700" : ""}`, onClick: () => handleClick("support") }, "Support"))),
                React.createElement("li", null,
                    React.createElement(Link, { to: "/help" },
                        React.createElement("p", { className: `py-2 px-3 rounded cursor-pointer ${isClicked == "help" ? "bg-blue-700" : ""}`, onClick: () => handleClick("help") }, "Help"))),
                React.createElement("li", null,
                    React.createElement("p", { className: `py-2 px-3 rounded cursor-pointer ${isClicked == "help" ? "bg-blue-700" : ""}`, onClick: handleExportData }, "Export")),
                React.createElement("li", null,
                    React.createElement("label", { htmlFor: "importFile", className: "py-2 px-3 rounded cursor-pointer bg-blue-700 text-white" },
                        "Import",
                        React.createElement("input", { id: "importFile", type: "file", accept: ".json", style: { display: "none" }, onChange: handleImportData }))))),
        React.createElement("div", null)));
};
export default Navbar;
